<template>
    <div class="home">
        <section class="py-5 text-center container">
            <div class="row py-lg-5">
                <div class="col-lg-6 col-md-8 mx-auto">
                    <h1 class="fw-light">헤더한번 꾸며봤다</h1>
                    <p class="lead text-body-secondary">주말에 기능 하나 올려본다!!</p>
                    <p>
                        <router-link to="/test" class="btn btn-primary my-2">Test 페이지로 이동</router-link>
                        <a href="#" class="btn btn-secondary my-2">Secondary action</a>
                    </p>
                </div>
            </div>
        </section>
        <div class="album py-5 bg-body-tertiary">
            <div class="container">
                <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3">
                    <div class="col" v-for="(item, i) in items" :key="i">
                        <Card :item="item" />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Card from '../components/main/Card.vue'
import axios from 'axios'

export default {
    name: 'Home',
    components: {
        Card
    },
    data() {
        return {
            items: []
        };
    },
    methods: {
        fetchItems() {
            axios.get('/api/items')
                .then(response => {
                    this.items = response.data;
                })
                .catch(error => {
                    console.error(error);
                });
        }
    },
    mounted() {
        this.fetchItems();
    }
}
</script>
